  font-size: 24px;
  font-weight: 400;
}
.extra__title::before {
  content: "";
  position: absolute;
  height: 3px;
  width: 50%;
  background-color: #68b738;
  top: 15px;
  right: 0;
  z-index: -1;
}
.extra__title span {
  padding-right: 23px;
  padding-left: 20px;
  background-color: #ffffff;
  z-index: 100;
}
