.container {
  width: 320px;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {
  .container {
    width: 708px;
    padding: 0 30px;
    margin: 0 auto;
  }
}

@media (min-width: @desktop-width) {
  .container {
    width: 1220px;
    padding: 0 40px;
    margin: 0 auto;
  }

  .container--desktop {
    width: 1220px;
    padding: 0 40px;
    margin: 0 auto;
  }
}
